
a {
  text-decoration: none;
}

svg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.scene-container {
  position: absolute;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  left: 0;
  right: 0;
  height: 100%;
}
.editor {
  position: absolute;
  z-index: 1;
  background-color: rgba(230, 230, 230, 0.8);
  padding: 0 12px;
}
.editor input {
  width: 230px;
}

.editor ul {
  padding: 0;
}

.editor li {
  list-style-type: none;
}

@media (max-width: 600px) {
  .editor {
    display: none;
  }
}



